import uuid from 'react-uuid';

function getAccomplishments() {
    const accomplishmentsJson = localStorage.getItem("accomplishments");
    if(accomplishmentsJson != null) {
        return JSON.parse(accomplishmentsJson);
    }
    return [];
}

//temp
function getAccomplishmentsAsList() {
    return Object.values(getAccomplishments());
}

function saveAccomplishmentFromState(state) {
    var id;
    if(state.isEditing) {
        id = state.id
    } else {
        id = uuid();
    }
    var accomplishments = getAccomplishments();

    accomplishments[id] = {
        title: state.title,
        description: state.description,
        imgUrl: state.imgUrl,
        dateCreated: Date.now(),
        id: id
    }

    setAccomplishments(accomplishments);
}

function setAccomplishments(accomplishments) {
    const accomplishmentsJson = JSON.stringify({...accomplishments});
    localStorage.setItem("accomplishments", accomplishmentsJson);
    window.dispatchEvent(new Event('storageUpdate'));
}

function deleteAccomplishmentById(id) {
    var accomplishments = getAccomplishments();
    delete accomplishments[id];

    setAccomplishments(accomplishments);
}

export {getAccomplishments, setAccomplishments, getAccomplishmentsAsList, saveAccomplishmentFromState, deleteAccomplishmentById};