import Button from 'react-bootstrap/Button';
import PropTypes from 'prop-types'
import Modal from 'react-bootstrap/Modal';
import { confirmable, createConfirmation } from 'react-confirm';

const ConfirmationModal = ({ show, proceed, confirmation }) => {
    return (
        <Modal show={show} onHide={() => proceed(false)}>
            <Modal.Header closeButton>
                <Modal.Title>Confirmation</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {confirmation}
            </Modal.Body>
            <Modal.Footer>
            <Button variant="danger" onClick={() => proceed(false)}>
                    No
                </Button>
                <Button variant="success" onClick={() => proceed(true)}>
                    Yes
                </Button>
            </Modal.Footer>
        </Modal>
    );
};

ConfirmationModal.propTypes = {
    show: PropTypes.bool,
    proceed: PropTypes.func,
    confirmation: PropTypes.string
};


export function confirm(
    confirmation,
    options = {}
) {
    return createConfirmation(confirmable(ConfirmationModal))({
        confirmation,
        ...options
    });
}