import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Form, InputGroup } from 'react-bootstrap';
import React, { Component } from 'react';
import { getAccomplishments, saveAccomplishmentFromState } from '../util/LocalStorage';

const initialState = {
    isEditing: false,
    show: false,
    validated: false,
    title: '',
    description: '',
    imgUrl: '',
    id: null,
    titleLength: 0,
    descriptionLength: 0
};

class AccomplishmentModal extends Component {

    constructor(props) {
        super(props);
        this.state = initialState;

        /* If type is edit, load values to edit */
        if (props.type === "edit") {
            let title = props.accomplishment.title;
            let description = props.accomplishment.description;
            this.state = {
                isEditing: true,
                show: false,
                validated: false,
                title: title,
                description: description,
                imgUrl: props.accomplishment.imgUrl,
                id: props.accomplishment.id,
                titleLength: title.length,
                descriptionLength: description.length
            };
        } else {
            this.state = initialState;
        }

        this.handleShow = this.handleShow.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.updateTitle = this.updateTitle.bind(this);
        this.updateDescription = this.updateDescription.bind(this);
        this.updateImgUrl = this.updateImgUrl.bind(this);
    }

    handleShow() {
        this.setState({ show: true });
    }

    handleClose() {
        this.setState({ show: false });
    }

    handleSubmit(e) {
        const form = e.currentTarget;
        const title = this.state.title;
        const description = this.state.description;

        if (form.checkValidity() === false ||
            title.length == 0 || title.length > 50 || description.length > 200) {
            e.preventDefault();
            e.stopPropagation();
        } else { //valid
            saveAccomplishmentFromState(this.state);

            if(this.state.isEditing) {
                this.setState({show: false});
            } else {
                this.setState(initialState);
            }
        }

        this.setState({ validated: true });
    };

    updateTitle(e) {
        const value = e.target.value;
        const valueLength = value.length;

        this.setState({ title: value, titleLength: valueLength });
    }

    updateDescription(e) {
        const value = e.target.value;
        const valueLength = value.length;

        this.setState({ description: value, descriptionLength: valueLength });
    }

    updateImgUrl(e) {
        const value = e.target.value;
        this.setState({ imgUrl: value })
    }

    render() {
        // Button that prompts opening the dialog
        // i.e. "Add Accomplishment" or "Edit Accomplishment buttons" respectively
        // TODO: Currently the only difference is the text, this will change.
        var dialogButton;
        console.log(this.state);
        if(this.state.isEditing) {
            dialogButton = <Button variant="primary" onClick={this.handleShow}>Edit</Button>;
        } else {
            dialogButton = <Button variant="primary" onClick={this.handleShow}>Add</Button>
        }
        return (
            <>
                {dialogButton}
                <Modal show={this.state.show} onHide={this.handleClose} backdrop="static"
                    keyboard={false}>
                    <Modal.Header closeButton>
                        <Modal.Title>{this.state.isEditing
                            ? "Editing Accomplishment" :
                            "New Accomplishment"}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form noValidate onSubmit={this.handleSubmit} validated={this.state.validated}>
                            <Form.Group className="mb-3" controlId="formTitle">
                                <Form.Label>Title</Form.Label>
                                <InputGroup hasValidation>
                                    <Form.Control type="text" onChange={this.updateTitle} value={this.state.title} maxLength={50} required />
                                    <Form.Control.Feedback type="invalid">
                                        Please enter a valid title.
                                    </Form.Control.Feedback>
                                </InputGroup>
                                <Form.Text className="text-muted">
                                    {50 - this.state.titleLength} characters remaining
                                </Form.Text>
                            </Form.Group>

                            <Form.Group className="mb-3" controlId="formDescription">
                                <Form.Label>Description (optional)</Form.Label>
                                <InputGroup hasValidation>
                                    <Form.Control as="textarea" value={this.state.description} onChange={this.updateDescription} maxLength={200} />
                                    <Form.Control.Feedback type="invalid">
                                        Please enter a valid description.
                                    </Form.Control.Feedback>
                                </InputGroup>
                                <Form.Text className="text-muted">
                                    {200 - this.state.descriptionLength} characters remaining
                                </Form.Text>
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="formTitle">
                                <Form.Label>Image url (optional)</Form.Label>
                                <InputGroup hasValidation>
                                    <Form.Control type="url" pattern="https://.*" value={this.state.imgUrl} onChange={this.updateImgUrl} />
                                    <Form.Control.Feedback type="invalid">
                                        Please enter a valid image url.
                                    </Form.Control.Feedback>
                                </InputGroup>
                            </Form.Group>
                        </Form>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={this.handleClose}>
                            Close
                        </Button>
                        <Button variant="primary" onClick={this.handleSubmit}>
                            Save
                        </Button>
                    </Modal.Footer>
                </Modal>
            </>
        );
    }
}
export default AccomplishmentModal;