import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import { deleteAccomplishmentById } from '../util/LocalStorage';
import { confirm } from './ConfirmationModal';
import React from 'react';
import AccomplishmentModal from './AccomplishmentModal';

class AccomplishmentCard extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      title: this.props.title,
      description: this.props.description,
      imgUrl: this.props.imgUrl,
      id: this.props.id
    };
    this.confirmDelete = this.confirmDelete.bind(this);
  }

  async confirmDelete() {
    if(await confirm("Are you sure you want to delete this Accomplishment?")) {
      deleteAccomplishmentById(this.state.id);
    }
  }

  render() {
    var img = <></>;
    if(this.props.imgUrl != undefined || this.props.imgUrl != "") {
        img = <Card.Img variant="top" src={this.props.imgUrl} />;
    }

    return (
        <Card style={{ width: '18rem' }}>
        {img}
        <Card.Body>
          <Card.Title className="text-black">{this.props.title}</Card.Title>
          <Card.Subtitle className="mb-2 text-muted">
            {this.props.description}
          </Card.Subtitle>
          <AccomplishmentModal type="edit" accomplishment={this.state} />
          <Button variant="danger" onClick={this.confirmDelete}>Delete</Button>
        </Card.Body>
      </Card>
    );
  }

}

export default AccomplishmentCard;