import { getAccomplishmentsAsList } from '../util/LocalStorage';
import AccomplishmentCard from './AccomplishmentCard';
import React from 'react';

class CardView extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            accomplishments: getAccomplishmentsAsList()
        }
        window.addEventListener('storageUpdate', () => {
            this.setState({ accomplishments: getAccomplishmentsAsList() });
        });
    }

    render() {
        return (
            <>
                {this.state.accomplishments.map((accomplishment, i) => {
                    return (
                        <AccomplishmentCard key={accomplishment.id}
                            title={accomplishment.title}
                            description={accomplishment.description}
                            imgUrl={accomplishment.imgUrl}
                            id={accomplishment.id} />
                    );
                })}
            </>
        );
    }
}

export default CardView;